/* CONTENT EDITOR */

//import Trumbowyg from 'react-trumbowyg';
//import 'react-trumbowyg/dist/trumbowyg.min.css'
//import 'trumbowyg/dist/trumbowyg.min.js'
import 'trumbowyg/dist/trumbowyg.js'
import 'trumbowyg/dist/ui/trumbowyg.min.css'
import {Editor, EditorState, RichUtils, convertFromHTML} from 'draft-js';
import {stateToHTML} from 'draft-js-export-html';
var stateFromHTML = require('draft-js-import-html').stateFromHTML;
var ReactBootstrap = require('react-bootstrap');

export default class ContentEditor extends React.Component {
  constructor(props) {
    super(props);
    this.isClosing = false;
    //this.state = { editorState: EditorState.createWithContent(stateFromHTML(this.props.content)), showHtmlDialog:false, html:props.content }
    this.state = { showHtmlDialog:false, html:props.content }
    //this.state = { editorState: this.importHtml(this.props.content).editorState, showHtmlDialog:false, html:'' }
    //this.changeEditor = (editorState) => { this.setState({editorState}); this.props.contentEditorValue(stateToHTML(this.state.editorState.getCurrentContent())); }
    //this.changeEditor = (e) => { console.log($('#trumbowyg-control')); this.setState({ html:e.target.innerHTML }); };
    //this._onInlineStyleClick = (type) => this.changeEditor(RichUtils.toggleInlineStyle(this.state.editorState, type));
    //this._onBlockTypeClick = (type) => this.changeEditor(RichUtils.toggleBlockType(this.state.editorState, type));
    this.showHtmlDialog = (toggle) => { this.setState({showHtmlDialog:toggle}, ()=> { if (toggle) this.initEditor() }); };
    //this.showHtmlDialog = (toggle) => { this.setState({showHtmlDialog:toggle}); if (toggle) { this.setState({ html:stateToHTML(this.state.editorState.getCurrentContent())}) } };
    this.htmlEditor_change = (e) => this.setState({html:e.target.value});
    this.focused = false;
  }

  componentWillReceiveProps(newProps) {
    //if (!this.state.showHtmlDialog && !this.focused && newProps.content != stateToHTML(this.state.editorState.getCurrentContent())) {
    if (!this.state.showHtmlDialog && !this.focused && newProps.content != this.state.html) {
      //this.setState({ editorState:EditorState.createWithContent(stateFromHTML(newProps.content)) });
      this.setState({ html:newProps.content });
    }
    return false;
  }

  shouldComponentUpdate(nextProps, nextState) {
    if (this.state.showHtmlDialog && !this.isClosing) {
      return false;
    }
    return true;
  }

  initEditor() {
    //const svg = require ('trumbowyg/dist/ui/icons.svg');
    $(this.refs.trumbowyg).trumbowyg({svgPath:false});
    $(this.refs.trumbowyg).trumbowyg('html', this.state.html.replace(/^<div>/,'').replace(/<\/div>$/,''));
    $('.trumbowyg-viewHTML-button').addClass('fa fa-code').text('');
    $('.trumbowyg-undo-button').addClass('fa fa-undo').text('');
    $('.trumbowyg-redo-button').addClass('fa fa-repeat').text('');
    $('.trumbowyg-formatting-button').addClass('fa fa-paragraph').text('');
    $('.trumbowyg-strong-button').addClass('fa fa-bold').text('');
    $('.trumbowyg-em-button').addClass('fa fa-italic').text('');
    $('.trumbowyg-del-button').addClass('fa fa-strikethrough').text('');
    $('.trumbowyg-superscript-button').addClass('fa fa-superscript').text('');
    $('.trumbowyg-subscript-button').addClass('fa fa-subscript').text('');
    $('.trumbowyg-link-button').addClass('fa fa-link').text('');
    $('.trumbowyg-insertImage-button').addClass('fa fa-image').text('');
    $('.trumbowyg-justifyLeft-button').addClass('fa fa-align-left').text('');
    $('.trumbowyg-justifyCenter-button').addClass('fa fa-align-center').text('');
    $('.trumbowyg-justifyRight-button').addClass('fa fa-align-right').text('');
    $('.trumbowyg-justifyFull-button').addClass('fa fa-align-justify').text('');
    $('.trumbowyg-unorderedList-button').addClass('fa fa-list-ul').text('');
    $('.trumbowyg-orderedList-button').addClass('fa fa-list-ol').text('');
    $('.trumbowyg-horizontalRule-button').addClass('fa fa-minus').text('');
    $('.trumbowyg-removeformat-button').addClass('glyphicon glyphicon-ban-circle').text('');
    $('.trumbowyg-fullscreen-button').addClass('glyphicon glyphicon-fullscreen').text('');
  }

/*
  importHtml(html) {
    const blocksFromHTML = convertFromHTML(sampleMarkup);
    const state = ContentState.createFromBlockArray(
      blocksFromHTML.contentBlocks,
      blocksFromHTML.entityMap,
    );
    return { editorState: EditorState.createWithContent(state) };
  }
*/

  htmlEditor_close(isSaving) {
    if (isSaving) {
      //this.setState({ editorState: EditorState.createWithContent(stateFromHTML(this.state.html)) });
      var content = $(this.refs.trumbowyg).trumbowyg('html');
      //console.log(content);
      this.props.contentEditorValue(content);
      this.isClosing = true;
      this.setState({html:content});
    } else
      this.isClosing = true;
    this.showHtmlDialog(false);
  }

  onFocus(isFocused) {
    this.focused = isFocused;
  }


  render() {
    var FormGroup = ReactBootstrap.FormGroup;
    var FormControl = ReactBootstrap.FormControl;
    var ListGroup = ReactBootstrap.ListGroup;
    var ListGroupItem = ReactBootstrap.ListGroupItem;
    var OverlayTrigger = ReactBootstrap.OverlayTrigger;
    var Tooltip = ReactBootstrap.Tooltip;
    var Button = ReactBootstrap.Button;
    var Modal = ReactBootstrap.Modal;

    return ( 
  
    <div className="ContentEditor" style={{position:'relative'}}>
{/*
      <div className="panel">
        <Button onClick={this._onInlineStyleClick.bind(this,'header-one')}><span style={{fontSize:1.4+'em'}}>H1</span></Button>
        <Button onClick={this._onInlineStyleClick.bind(this,'header-two')}><span style={{fontSize:1.3+'em'}}>H2</span></Button>
        <Button onClick={this._onInlineStyleClick.bind(this,'header-three')}><span style={{fontSize:1.2+'em'}}>H3</span></Button>
        <Button onClick={this._onInlineStyleClick.bind(this,'header-four')}><span style={{fontSize:1.1+'em'}}>H4</span></Button>
        <Button onClick={this._onInlineStyleClick.bind(this,'unordered-list-item')}><span className="fa fa-list-ul"></span></Button>
        <Button onClick={this._onInlineStyleClick.bind(this,'ordered-list-item')}><span className="fa fa-list-ol"></span></Button>
 &nbsp; 
*/}
{/*
        <Button onClick={this._onInlineStyleClick.bind(this,'BOLD')}><span className="active" style={{fontSize:0.8+'em'}}><strong>B</strong></span></Button>
        <Button onClick={this._onInlineStyleClick.bind(this,'ITALIC')}><span className="active" style={{fontSize:0.8+'em'}}><em>I</em></span></Button>
        <Button onClick={this._onInlineStyleClick.bind(this,'UNDERLINE')}><span className="active" style={{fontSize:0.8+'em'}}><u>U</u></span></Button>
 &nbsp;
        <Button onClick={this.showHtmlDialog.bind(this,true)}>HTML</Button>
        <div className="overlay" style={{position:'absolute', height:'100%', width:'100%'}} onClick={this.showHtmlDialog.bind(this,true)}></div>
*/}
        <div className="well" onClick={this.showHtmlDialog.bind(this,true)}>
          <iframe style={{width:'100%', height:'100%', backgroundColor:'#fff'}} srcDoc={(this.props.headHtml||"") + this.state.html} />
{/*
          <iframe style={{width:'100%', height:'100%', backgroundColor:'#fff'}} srcDoc={(this.props.headHtml||"") + this.state.html} onClick={this.showHtmlDialog.bind(this,true)} />
          <Editor editorState={this.state.editorState} type="content" onChange={this.changeEditor.bind(this)} onFocus={this.onFocus.bind(this,true)} onBlur={this.onFocus.bind(this,false)} style={{width:'inherit'}} />
*/}
          <Button onClick={this.showHtmlDialog.bind(this,true)}><span className="fa fa-pencil"></span> Edit</Button>
        </div>
{/*
      </div> 
*/}
      <Modal show={this.state.showHtmlDialog} onHide={this.htmlEditor_close.bind(this,false)}>
        <Modal.Header closeButton>
          <h3>HTML Editor</h3>
        </Modal.Header>
        <Modal.Body>
          <div ref='trumbowyg'></div>
          {/*<Trumbowyg ref='trumbowyg' id='trumbowyg-control' data={this.state.html} onChange={this.changeEditor.bind(this)} />*/}
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={this.htmlEditor_close.bind(this,true)}>OK</Button> <Button onClick={this.htmlEditor_close.bind(this,false)} className="pull-right">Cancel</Button>
        </Modal.Footer>
      </Modal>
      </div>

      );
  }
}



