import PropTypes from 'prop-types'
var ReactDOM = require('react-dom');
import { LeftNav, TopNav } from './components/core/nav.js'
import MainContent from './components/core/main_content.js'
import Account from './components/common/account.js'
import s from './components/core/custom.css'

class MainApp extends React.Component {
  constructor() {
    super();
    this.system = {
      url:"https://api.influentmetrics.io/prod/Dashboard"
    };
    this.changeTab = this.changeTab.bind(this);
    this.jumpAttributes = {};
    //this.state = { nav:'overview', settings:{}, account:{plan:'lite'} };
    this.state = { nav:'overview', settings:{}, account:null };
  }
  changeTab(tab) {
    this.setState({ nav:tab });
  }

  setSettings(settings) {
    this.setState({ settings:settings });
  }
  setAccount(account) {
    account.org = account.orgs[0];
    this.setState({ account:account });
  }
  setOrg(org) {
    var account = this.state.account;
    account.org = org;
    this.setState({ account:account }, this.refs.MainContent.getData());
    //this.refs.MainContent.getData();
  }
  sendProfileDate(profileDate) {
    // send downstream for data updates to the control
    this.setState({ profileDate:profileDate });
  }
  setProfileDate(date) {
    // send upstream to TopNav for date change handler
    this.refs.MainContent.setProfileDate(date);
  }

  jumpTab(tab, attributes) {
    this.jumpAttributes = {};
    Object.keys(attributes).map(function(key, index) {
      this.jumpAttributes[key] = attributes[key];
    }, this);
    this.setState({nav:tab});
    $('#sidebar-menu .nav-'+tab+' > a').click();
  }


  render() {
    var attributes = this.jumpAttributes;
    this.jumpAttributes = {};
    if (this.state.account) {
      return (
<div className="Container">
  <LeftNav changeTab={this.changeTab} nav={this.state.nav} account={this.state.account} settings={this.state.settings} />
  <div className="Dashboard"><TopNav changeTab={this.changeTab} setOrg={this.setOrg.bind(this)} profileDate={this.state.profileDate} setProfileDate={this.setProfileDate.bind(this)} settings={this.state.settings} account={this.state.account} /><MainContent ref="MainContent" system={this.system} nav={this.state.nav} account={this.state.account} sendProfileDate={this.sendProfileDate.bind(this)} setSettings={this.setSettings.bind(this)} {...attributes} jumpTab={this.jumpTab.bind(this)} /></div>
</div>
      );
    } else {
      return (
<div className="Container" style={{ width:100+'%', height:100+'%', position:'absolute', background:'url(https://influentmetrics.com/assets/img/backgrounds/backgroundLogo.png) rgb(154, 1, 1)'}}>
  <Account system={this.system} setAccount={this.setAccount.bind(this)} />
</div>
      );
    }
  }
};


ReactDOM.render(
  <MainApp/>,
  document.getElementById('content')
);




